.finder-container{
    width: 90%;
    background-color: #161b22; 
}
.finder-container h1{
    color: #fff;
    padding-top: 25px;
}
.finder-searcher{
    background-color: #161b22; 
    padding-left: 50px;
    padding-top: 15px;
    height: 50px;
}
.finder-searcher .input{
    height: 30px;
    width: 400px;
    border-radius: 10px;
}
.submit-button{
margin-left: 1%;
width: 100px;
height: 20px;
height: 30px;
border-radius: 5px;
}
.githup-finder{
    height: 100%;
    display: flex;
    width: 80%;
    color: aliceblue;
}
.finder-repo a {
    font-weight: bold;
    color: aliceblue;
justify-content: center;   
     text-decoration: none;    /* Remove underline */
    transition: color 0.2s;
}
.finder-repo ul{
list-style-type: none;

}
.finder-name{
 color: aliceblue;
 padding-left: 5%;
 padding-top: 0px;


}
.finder-repo {
    text-align: start;
    padding-top: 50px;
    padding-left: 5%;
    color: aliceblue;
    text-decoration: none;
}

@media (max-width: 769px) {
    .finder-container h1{
        padding-left: 3%;
        font-size: 20px;
    }
    .finder-searcher{
        background-color: #161b22; 
        padding-left: 150px;
        padding-top: 15px;
        height: 50px;
    }
    .finder-searcher .input{
    height: 30px;
    width: 300px;
    height: 30px;
    border-radius: 10px;
}
.finder-searcher{
    height: 10px;
}
.githup-finder{
    padding: 3%;
    display: block;
    margin-top: 5%;
    width: 80%;
}
.finder-name{
    width: 30%;
}
.input{
    height:100px;
}
.submit-button{
    height: 20px;
}
.finder-repo{
    padding-top: 2%;
}
}
